#game-and-title{display:flex;flex-direction:column;max-width:1100px;margin:0 auto;position:relative}#title{text-align:center;font-size:24px;margin-top:0;margin-bottom:0;font-family:'Fjalla One', sans-serif}#main-canvas{box-shadow:0 0 5px 2px;max-width:1100px;max-height:700px}#start-menu{width:1100px;height:700px;max-width:1100px;max-height:700px;display:flex;flex-direction:column;justify-content:center;align-items:center;position:absolute;top:30px;background:black}#play-button{min-width:200px;min-height:100px;box-shadow:0 0 5px 2px;font-size:20px;text-align:center;line-height:100px;background:gray;font-family:'Fjalla One', sans-serif}#play-button:hover{background:black;color:white}#instructions{min-width:200px;min-height:100px;background:gray;color:black;line-height:.5;text-align:center;margin-top:50px;font-family:'Fjalla One', sans-serif}body{background-image:url("/../../../images/bg5.jpg")}
